
.Frame {
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-grow: 1;
    position: relative;
    flex-direction: column;
    min-width: 280px;
    &.is-resizing {
        cursor: grabbing;
        .Frame-body,
        .Frame-panel {
            user-select: none;
            pointer-events: none;
        }
    }
}
// .Frame-header {
