.Frame-body {
    display: flex;
    flex-direction: row-reverse;
    flex-grow: 1;
    height: 100%;
    @include mq($until: navCollapse) {
        @include margin-inline('end', 230px);
        transition: none;
        transform: translate3d(-230px, 0, 0);
    }
}
.Frame-inner {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    min-width: 280px;
}

.Frame-panel {
    height: calc(100% - 50px);
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
}
.Frame-panel--sidebar {
    @include border-inline('end', 1px solid $color-alpha-tint-light);
    flex: 0 0 auto;
    background-color: $color-gamma;
    width: 230px;
    min-width: 230px;
    max-width: 400px;
    padding: 25px 0 40px 0;
    @include mq($until: navCollapse) {
        max-width: 230px;
    }
}
.Frame-panel--main {
    flex: 1 1 auto;
    overflow: auto;
    position: relative;
    z-index: 1;
    padding-bottom: 1px;
    [dir="ltr"] & {
        margin-left: -20px;
    }
    [dir="rtl"] & {
        margin-right: -20px;
    }
    &.is-loading {
        background-image: url('../img/loader.svg');
        background-repeat: no-repeat;
        background-position: 50%;
        .Frame-inner {
            display: none;
        }
    }
}
.Frame-handle {
    flex: 0 0 auto;
    position: relative;
    z-index: 2;
    width: 20px;
    cursor: ew-resize;
    height: 100%;
    background: transparent url('../img/icon-drag--vertical.svg') no-repeat 50% 45%;
    background-size: 3px 16px;
    @include dragHandle;
}
